.sidebar {
    background-color: #32BE7F;
    background: linear-gradient(180deg, #27B983 -18.15%, #91E857 266.75%);
    box-shadow: inset -1px 0px 0px rgba(28, 175, 94, 0.1);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 0 0 15rem;


    &__logo {
        text-align: center;
        background: rgba(255, 255, 255, 0.1);
        padding: 1.25rem 0 1.563rem;

    }



    &__nav {
        padding: 0 1.25rem;

        &Item {
            padding: 1rem 1.438rem;
            border-radius: 5px;

            &:hover {
                background-color: $darckGreen;
                box-shadow: inset 0 0 0 2px $Green;
            }




        }

        &Link {
            color: $white;
        }
    }
}


.selected {
    background: rgba(255, 255, 255, 0.15);
}

.user {
    display: flex;
    padding: 2.438rem 1.25rem;


    &__avatar {
        border: 2px solid #ffffff;
        border-radius: 50%;
        margin-right: 0.625rem;

    }

    &__info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        line-height: 1.4;

    }

    &__name {
        font-size: 0.875rem;
        color: $white;
    }

    &__count {
        font-size: 0.75rem;
        color: $darckGreen;
    }
}

.help {


    &__list {
        text-align: center;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        padding: 0 1.25rem;
        padding-bottom: 1.875rem;

        &::before {
            display: inline-block;
            height: 1px;
            width: 100%;
            content: '';
            border-top: 1px solid $devider;
            margin-bottom: 1.375rem;
        }
    }

    &__item {
        width: 33%;
        font-size: 0.75rem;
        padding-bottom: 0.875rem;

        &:hover:after {
            display: block;
            content: "";
            width: 100%;
            border-bottom: 2px solid $Green;
            margin-top: 0.5rem;
        }
    }

    &__link {
        color: $darckGreen;
    }

}


@media screen and (max-width: 1290px) {
    .help {
        position: fixed;
        bottom: 1.25rem;
        left: 1.25rem;
        padding: 0;
    }
}